body {
  --screen-xl: $screen-xl-size;
  --screen-lg: 1024px;
  --screen-md: 786px;
}

/* stylelint-disable-line at-rule-no-unknown */
/* ================== ТИПОГРАФИКА ================== */
.page :root {
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 14px;
}
.page :root * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.page body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}
.page__inner {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
}
.page__content {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
}

.cot {
  display: flex;
  flex-direction: column;
  padding: 4rem 3rem;
  font-family: sans-serif;
}
.cot__title {
  margin: 2rem 0 1rem;
  padding: 0.25rem 0;
  border-bottom: 1px solid #aeaeae;
  font-size: 1.5rem;
}
.cot__subtitle {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
  margin: 1rem 0 0.5rem;
  font-size: 1.25rem;
}
.cot__item {
  margin: 0.5rem 0;
  font-size: 1.25rem;
}

body:has(.text-zoom-panel) .book-container :root {
  margin: unset;
  padding: unset;
}
body:has(.text-zoom-panel) .ScopeBody {
  margin-top: var();
}
body:has(.text-zoom-panel) .text-zoom-panel {
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 25;
  width: 100%;
  height: 50px;
  background-color: #ddd;
  box-shadow: 0 6px 7px 0 rgba(34, 60, 80, 0.2);
  font-family: Calibri, Helvetica, Arial, sans-serif;
}
body:has(.text-zoom-panel) .text-zoom-panel__wrapper {
  display: flex;
  width: max-content;
  margin: 0 auto;
}
body:has(.text-zoom-panel) .text-zoom-panel__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  width: 25px;
  height: 25px;
  margin: 0 10px;
  padding: 0;
  border: 1px solid #444;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}
body:has(.text-zoom-panel) .text-zoom-panel__btn:hover {
  opacity: 0.75;
}
body:has(.text-zoom-panel) .text-zoom-panel__btn--additional {
  width: max-content;
  padding: 0 0.5rem;
}
body:has(.text-zoom-panel) .text-zoom-panel__btn-ico {
  margin-top: 2px;
  font-size: 0.75rem;
}
body:has(.text-zoom-panel) .text-zoom-panel__info {
  display: flex;
  align-self: stretch;
  align-items: center;
  min-width: 50px;
  margin-left: 20px;
}
body:has(.text-zoom-panel) .book-container-wrapper {
  display: flex;
  flex: 1;
  justify-content: center;
  width: 100%;
  padding: 2rem 0;
  box-shadow: 2px 3px 20px black, 0 0 60px #8a4d0f inset;
}
body:has(.text-zoom-panel) .book-container-wrapper .book-container {
  background: none;
}

body:has(.CSSVariablesExamples) .CSSVariablesExamples {
  flex: 1;
  box-sizing: border-box;
  width: 100%;
  min-height: 100%;
  padding: 2rem;
  background: #aaa;
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  width: 100%;
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .card {
  ---tmp-cl-card-bg: #ffd;
  --tmp-cl-card-text: #555;
  --tmp-card-padding: 10px;
  --tmp-card-column-span: span 1;
  --tmp-card-text-font-weight: 400;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  grid-column: var(--card-column-span, var(--tmp-card-column-span));
  width: auto;
  height: auto;
  padding: var(--card-padding, var(--tmp-card-padding));
  border-radius: 10px;
  background-color: var(--cl-card-bg, var(---tmp-cl-card-bg));
  font-family: var(--ff-card-font, sans-serif);
  color: var(--cl-card-text, var(--tmp-cl-card-text));
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .card__title {
  color: var(--cl-card-title, inherit);
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .card__text {
  font-weight: var(--card-text-font-weight, var(--tmp-card-text-font-weight));
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .card--active {
  --cl-card-bg: #212121;
  --cl-card-text: #ddd;
  --card-padding: 10px 25px;
  --card-column-span: span 2;
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .card--warning {
  --cl-card-title: #d44;
  --card-text-font-weight: 600;
}
body:has(.CSSVariablesExamples) .CSSVariablesExamples .card--warning.card--active {
  --cl-card-text: #999;
}

body:has(.VariablesInMediaQueries) :root {
  font-size: 16px;
}
body:has(.VariablesInMediaQueries) {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background: #bebebe;
  padding: 0;
  margin: 0;
}
body:has(.VariablesInMediaQueries) .VariablesInMediaQueries {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
body:has(.VariablesInMediaQueries) .VariablesInMediaQueries .block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 150px;
  border: 1px solid #000;
}
body:has(.VariablesInMediaQueries) .VariablesInMediaQueries .text {
  font-family: sans-serif;
  font-size: 1.5rem;
  color: #fff;
}